type LogLevel = 'INFO' | 'WARN' | 'ERROR' | 'CRITICAL'

interface LogEntry {
  timestamp: string
  level: LogLevel
  message: string
  data?: any
  stack?: string
  userId?: string
  ip?: string
  endpoint?: string
}

class Logger {
  private formatLog(entry: LogEntry): string {
    return JSON.stringify(entry)
  }

  info(message: string, data?: any) {
    const entry: LogEntry = {
      timestamp: new Date().toISOString(),
      level: 'INFO',
      message,
      data
    }
    if (process.env.NODE_ENV === 'development') {
      console.log(`[INFO] ${message}`, data || '')
    }
  }

  warn(message: string, data?: any) {
    const entry: LogEntry = {
      timestamp: new Date().toISOString(),
      level: 'WARN',
      message,
      data
    }
    console.warn(`[WARN] ${message}`, data || '')
  }

  error(message: string, error?: any, context?: { userId?: string; ip?: string; endpoint?: string }) {
    const sanitizedError = error instanceof Error ? {
      message: error.message,
      name: error.name
    } : error
    const entry: LogEntry = {
      timestamp: new Date().toISOString(),
      level: 'ERROR',
      message,
      data: sanitizedError,
      stack: process.env.NODE_ENV === 'development' ? error?.stack : undefined,
      ...context
    }
    console.error(`[ERROR] ${message}`, sanitizedError)
  }

  critical(message: string, error?: any, context?: { userId?: string; ip?: string; endpoint?: string }) {
    const sanitizedError = error instanceof Error ? {
      message: error.message,
      name: error.name
    } : error
    const entry: LogEntry = {
      timestamp: new Date().toISOString(),
      level: 'CRITICAL',
      message,
      data: sanitizedError,
      stack: process.env.NODE_ENV === 'development' ? error?.stack : undefined,
      ...context
    }
    console.error(`[CRITICAL] ${message}`, sanitizedError)
  }
}

export const logger = new Logger()
